import cys from './config.js'
export default {
	data() {
		return {
			getWindowTop: 0,
			moveType: '',
			touchObj: {
				startX: 0,
				startY: 0,
				moveX: 0,
				moveY: 0
			}
		}
	},
	props: {
		msg: {
			type: String,
			default() {
				return '成功'
			}
		},
		type: {
			type: String,
			default() {
				return 'success'
			}
		},
		moveTypes: {
			type: String,
			default() {
				return ''
			}
		}
	},
	watch: {
		moveTypes(str) {
			this.moveType = str
		}
	},
	created() {
		let that = this



		uni.getSystemInfo({
			success: function(res) {
				if (res.model.indexOf('iPhone') !== -1) {

					that.getWindowTop = 10 + 'px'

				} else {

					that.getWindowTop = 10 + 'px'

				}
			}
		})
	},
	methods: {
		moveStart(e) {
			this.touchObj.startY = e.touches[0].pageY
			this.touchObj.startX = e.touches[0].pageX
			
			// cys.log(this.touchObj)
		},
		moveM(e) {
			let that = this
			that.touchObj.moveY = e.touches[0].pageY
			that.touchObj.moveX = e.touches[0].pageX
			
			if ((e.touches[0].pageX - that.touchObj.startX) > 10) {
				that.moveType = 'right'
			} else if ((e.touches[0].pageX - that.touchObj.startX) < -10) {
				that.moveType = 'left'
			} else {
				that.moveType = 'top'
			}
			
			setTimeout(function() {
				that.$emit('reduction')
			}, 500)
			
			// cys.log(this.touchObj)
		},
		moveEnd(e) {
			cys.log(this.touchObj)
			// if (this.touchObj.startY > )
		}
	}
}
